import React, {useEffect} from 'react';
import { motion } from "framer-motion";
import FadeIn from "react-lazyload-fadein";

function choosePara(status) {
  const lineHeight = 24; //Each text line adds 24px to height
        switch (status) {
          case 0:
            return <div className="text-container">
            <motion.h1 className="education-text-h"
            key="/education_text_h"
            initial={{ opacity: 0, x:0, y:20 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	Awards
            </motion.h1>
            <motion.p className="education-text-1"
            key="/education_text"
            initial={{ opacity: 0, x:0, y:50 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
              I have always maintained the belief that anything can be achieved through hard work and dedication.
              Combining that belief with passion and excitement for my studies, I have been able to deliver projects and get results I am proud of.
            </motion.p>
            <motion.p className="education-text-1"
            key="/education_text_2"
            initial={{ opacity: 0, x:0, y: 103 + 2 * lineHeight}}
            animate={{ opacity: 1, x:0, y: 63 + 2 * lineHeight}}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}>
        	  	I have been fortunate enough to have been recognized for these projects and have received numerous awards and scholarships related to both my engineering degree and my MBA, which are detailed on this page.
            </motion.p>
            </div>
          case 1:
            return <div className="text-container">
            <motion.h1 className="education-text-h"
            key="/chalmers_text_h"
            initial={{ opacity: 0, x:0, y:20 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	Automation Student 2019
            </motion.h1>
            <motion.p className="education-text-1"
            key="/chalmers_text"
            initial={{ opacity: 0, x:0, y:50 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	I did my master's thesis at ABB with a focus on reinforcement learning and teaching a robot to find optimized solutions in RobotStudio.
            </motion.p>
            <motion.p className="education-text-2"
            key="/chalmers_text2"
            initial={{ opacity: 0, x:0, y: 60 + 1 * lineHeight}}
            animate={{ opacity: 1, x:0, y: 20 + 1 * lineHeight}}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}>
              Our work was submitted to Automation Region, a Swedish organization that focuses on industrial automation, and we won the award of the best automation related master thesis of the year in Sweden.
              Read more about our work and the award <a className="text_link_1" target="_blank" rel="noopener noreferrer" style={{color:'#ffbf00', fontWeight:'bold'}} href="https://www.automationregion.com/arbete-om-optimering-av-kollaborativa-robotar-vinnare-i-automation-student/#.XZV77UjDPzE.linkedin">
              here</a>.
            </motion.p>
            </div>
          case 2:
           return <div className="text-container">
           <motion.h1 className="education-text-h"
           key="/graz_text_h"
           initial={{ opacity: 0, x:0, y:20 }}
           animate={{ opacity: 1, x:0, y:0 }}
           exit={{ opacity: 0 }}
           transition={{ duration: 0.5, delay: 0 }}>
             W.A. Franke College of Business
           </motion.h1>
           <motion.p className="education-text-2"
           key="/graz_text"
           initial={{ opacity: 0, x:0, y:50 }}
           animate={{ opacity: 1, x:0, y:0 }}
           exit={{ opacity: 0 }}
           transition={{ duration: 0.5, delay: 0 }}>
             Once every year in Northern Arizona University, each faculty gets to award one student <span style={{color:'#ffbf00', fontWeight:'bold'}}>Graduate Student of the Month</span>.
             I was nominated by a professor and I was awarded the honor for the W.A. Franke College of Business.
           </motion.p>
           <motion.p className="education-text-3"
           key="/graz_text2"
           initial={{ opacity: 0, x:0, y: 60 + 2 * lineHeight}}
           animate={{ opacity: 1, x:0, y: 20 + 2 * lineHeight}}
           exit={{ opacity: 0 }}
           transition={{ duration: 0.5, delay: 0.2 }}>
             Furthermore, the MBA program gives out two awards each year: <span style={{color:'#ffbf00', fontWeight:'bold'}}>Outstanding MBA Student of the Year</span>, and the <span style={{color:'#ffbf00', fontWeight:'bold'}}>MBA Association Award for Student Excellence</span>.
             The recipient of the former is chosen by faculty while the latter is chosen by the students of the MBA cohort. I received both awards for my MBA year.
           </motion.p>
           </div>
          case 3:
            return <div className="text-container">
            <motion.h1 className="education-text-h"
            key="/nau_text_h"
            initial={{ opacity: 0, x:0, y:20 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	Dr. Tech. Marcus Wallenberg Foundation
            </motion.h1>
            <motion.p className="education-text-2"
            key="/nau_text"
            initial={{ opacity: 0, x:0, y:50 }}
            animate={{ opacity: 1, x:0, y:0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0 }}>
        	  	The Dr. Tech. Marcus Wallenberg Foundation for Education in International Industrial Entrepreneurship funds scholarships for master studies at well-reputed universities outside Sweden.
            </motion.p>
            <motion.p className="education-text-3"
            key="/nau_text2"
            initial={{ opacity: 0, x:0, y: 60 + 1 * lineHeight}}
            animate={{ opacity: 1, x:0, y: 20 + 1 * lineHeight}}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}>
              I received the award in 2019 when I decided to pursue an MBA at Northern Arizona University.
              The award covered all my expenses at NAU and enabled me to complete my studies without any financial worry. Read more about the award <a className="text_link_1" target="_blank" rel="noopener noreferrer" style={{color:'#ffbf00', fontWeight:'bold'}} href="https://tmw.wallenberg.org/en">
              here</a>.
            </motion.p>
            </div>
          default:
            return null;
  };
}

function Awards() {
  const [status, setStatus] = React.useState(0);
  const [custom, setCustom] = React.useState(1);
  const [[sizeBox1, sizeBox2, sizeBox3], setSizeVar] = React.useState([1,1,1]);

  useEffect(() => {
    setCustom(1);
    setCustom(0);
  }, []); /*do this on load only once*/

  return (
    <div className="container-fluid">
      <div className="box-container">
        <motion.div className="box1_awa"
          key="/automation_pic"
          transition={{ duration: 0.5, delay: custom * 0.25 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#ffbf00'}} //this is stupid, for some reason scale can't be changed twice
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox1, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 1) ? setStatus(1) : setStatus(0);
            (sizeBox1 === 1.1) ? setSizeVar([1,1,1]) : setSizeVar([1.1,1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/awards/automation_region.png" alt=""
          draggable="false"
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>

        <motion.div className="box2_awa"
          key="/franke_pic"
          transition={{ duration: 0.5, delay: custom * 0.5 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#ffbf00'}} //this is stupid, for some reason scale can't be changed twice
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox2, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 2) ? setStatus(2) : setStatus(0);
            (sizeBox2 === 1.1) ? setSizeVar([1,1,1]) : setSizeVar([1,1.1,1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/awards/nau_franke.png" alt=""
          draggable="false"
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>

        <motion.div className="box3_awa"
          key="wallenberg_pic"
          transition={{ duration: 0.5, delay: custom * 0.75 }}
          whileHover={{ scaleX: 1.05, scaleY: 1.05, backgroundColor: '#ffbf00'}} //this is stupid, for some reason scale can't be changed twice
          initial={{ opacity: 0, scale: 1, x: 0, y: 50 }}
          animate={{ opacity: 1, scale: sizeBox3, x:0, y: -10 }}
          exit={{ opacity: 0, scale: 1, transition: {staggerChildren: 0.5} }}
          onClick={() => {
            (status !== 3) ? setStatus(3) : setStatus(0);
            (sizeBox3 === 1.1) ? setSizeVar([1,1,1]) : setSizeVar([1,1,1.1]);
          }}
        >
        <FadeIn height={400} duration={200}>
        {onLoad =>
          <img src="/awards/wallenberg2.png" alt=""
          draggable="false"
          onLoad={onLoad} />
        }
        </FadeIn>
        </motion.div>
      </div>

      {choosePara(status)}
	  </div>
  );
}

export default Awards;
